import React from "react";
import axios from "axios";
import InfiniteScroll from "react-infinite-scroll-component";
import home from "./HomeMain.css";
import "lazysizes";


// 定义主要内容
class HomeMain extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data:[],
      page:1,
      param:'',
      err:''
    };
  }

  componentDidMount() {
    const params = this.props.props.location.search;
    const p = (new URLSearchParams(params)).get("language");
    this.setState({param:p});
    this.getData(p);
  }

  UNSAFE_componentWillReceiveProps(newProps){
    const {param} = newProps;
    if(param!==this.state.param){
      this.setState({
        data:[],
        param,
        page:1
      },()=>{
        this.getData(param);
      });
    }
  }

  componentWillUnmount = () => {
    this.setState = () => {};
  };

  // 请求数据
  getData = async param => {
    const {data,page} = this.state;
    let res = null;
    // 请求数据
    if (param === "all") {
      res = await axios({
        url:
          "https://api.github.com/search/repositories?q=stars:%3E1&sort=stars&order=desc&type=Repositories",
        params: {
          per_page: 10,
          page
        }
      })
      .catch(error=>{
        const err = error.response.data.message;
        this.setState({err});
      });
    } else {
      res = await axios({
        url: `https://api.github.com/search/repositories?q=stars:%3E1+language:${param}&sort=stars&order=desc&type=Repositories`,
        params: {
          per_page: 10,
          page
        }
      })
      .catch(error=>{
        const err = error.response.data.message;
        this.setState({err});
      });
    }

    const arr = [];
    res.data.items.forEach(item => {
      const obj = {
        id: item.id,
        url: item.owner.avatar_url,
        name: item.owner.login,
        infos: [
          {
            text: item.owner.login,
            icon: "fa-user",
            color: "#febf6b"
          },
          {
            text: item.watchers,
            end: " stars",
            icon: "fa-star",
            color: "#fed901"
          },
          {
            text: item.forks,
            end: " forks",
            icon: "fa-code-fork",
            color: "#a2cdef"
          },
          {
            text: item.open_issues,
            end: " open issues",
            icon: "fa-warning",
            color: "#f8868f"
          }
        ]
      };
      arr.push(obj);
    });
    this.setState({
        data: [...data, ...arr],
        page:page+1,
        err: ""
      });
  };

  render() {
    const {data,param,err} = this.state;
    let msg = "Loading ...";
    if (err !== "") {
      msg = '数据请求失败，请刷新页面重试！';
    } else {
      msg = "Loading ...";
    }
    return (
      <div className={`container ${home.home_box}`}>
        <InfiniteScroll
          style={{ overflow: "unset" }}
          dataLength={data.length}
          next={() => this.getData(param)}
          hasMore
          loader={
            <h4 className={home.loading} style={{color:err === "" ? '#000' : 'red'}}>
              <i
                className="fa fa-spinner fa-spin"
                style={{
                  display: err === "" ? "inline-block" : "none"
                }}
              />{msg}<br/><br/>{err}
            </h4>
          }
        >
          <ul className={`row ${home.project_box}`}>
            {data.map((item, idx) => (
              <li
                className={`col-9 col-sm-6 col-md-4 col-lg-3 ${home.project_item}`}
                key={idx}
              >
                <h2>#{idx + 1}</h2>
                <img data-src={item.url} alt="" className="lazyload" />
                <h3>{item.name}</h3>
                <ul className={`fa-ul ${home.icon_box}`}>
                  {item.infos.map(info => (
                    <li key={info.icon}>
                      <i
                        style={{ color: info.color }}
                        className={`icon fa ${info.icon}`}
                      >
                        <span>
                          {info.text}
                          {info.end}
                        </span>
                      </i>
                    </li>
                  ))}
                </ul>
              </li>
            ))}
          </ul>
        </InfiniteScroll>
      </div>
    );
  }
}
export default HomeMain;
