import React from "react";
import Header from "@/components/Home/Header";
import HomeMain from "@/components/Home/HomeMain";

class Home extends React.Component {
  
  constructor(props){
    super(props);
    this.state = {
      param:'all'
    }
  }

  getParam = (param)=>{
    this.setState({param});
    this.props.history.push(`/popular?language=${param}`);
  }

  render() {
    const {param} = this.state;
    return (
      <div style={{ flex: 1 }}>
        <Header props={this.props} onClick={this.getParam}/>
        <HomeMain props={this.props} param={param}/>
      </div>
    );
  }
}

export default Home;
